  '1333': {
    id: '1333',
    department: '電機系',
    tags: [ '電路' ],
    name: '數位系統',
    credit: 3,
    teacher: '林政宏 高文忠',
    time: "{'1': ['7', '8', '9'], '2': [], '3': [], '4': [], '5': [], '6': []}",
    location: '師大本部',
    semester: '107-1',
    description: '<p>This course provides a modern introduction to logic design and the basic building blocks used in digital systems, in particular digital computers. It starts with a discussion of combinational logic: logic gates, minimization techniques, arithmetic circuits, and modern logic devices such as field programmable logic gates. The second part of the course deals with sequential circuits: flip-flops, synthesis of sequential circuits, and case studies, including counters, registers, and random access memories. State machines will then be discussed and illustrated through case studies of more complex systems using programmable logic devices. Different representations including truth table, logic gate, timing diagram, switch representation, and state diagram will be discussed.</p><p> </p><p>Topics:</p><p>Outlines</p><p>Introduction of digital system and the trend of its development</p><p>Chap 1: Binary Systems and binary numbers</p><p>Chap 2: Boolean Algebra and Logic Gates</p><p>Chap 3: Gate-Level Minimization</p><p>Chap 4: Combinational Logic</p><p>Chap 5: Synchronous Sequential Circuit</p><p>Chap 6: Registers and counters</p><p>Chap 7: Memory and Programmable Logic</p><p>Chap 8: Design at the register transfer level</p><p>FPGA Design Flow</p><p>Verilog HDL introduction</p>'
  },
