  '2659': {
    id: '2659',
    department: '數學系',
    tags: [ 'MATLAB', ' 程式設計' ],
    name: '程式設計',
    credit: 3,
    teacher: '范洪源',
    time: "{'1': ['8', '9', '10'], '2': [], '3': [], '4': [], '5': [], '6': []}",
    location: '師大分部',
    semester: '107-2',
    description: '<font face="tahoma, arial, helvetica, sans-serif"><span style="font-size: 12px;"><br/></span></font><div> <p class="MsoNormal" style="font-size: 12px; font-family: tahoma, arial, helvetica, sans-serif; line-height: 22pt; "><span style="font-family:標楷體">今日為何科技會如此日新月異<span lang="EN-US">?</span>原因之一在於電腦計算能力不斷的提昇。因此如何有效的利用電腦來幫我們處理事情，顯得格外重要，所以在世界各國，科學計算領域已受到相當重視。而在科學計算領域中數值分析是一門基礎科目，它不同於其它數學基礎科目，數值分析是一門介紹如何利用電腦來處理我們所熟悉的數學問題，如求解線性聯立方程式、單一變數非線性函數零根、多變數非線性聯立方程式之解、矩陣之特徵值與特徵向量、積分與微分、微分方程之解等等，以及分析電腦計算所得之結果與實際真正的解有多少誤差。而這些目標必須仰賴程式設計做為基礎</span><span style="font-family: 標楷體; ">，透過撰寫程式來控制電腦完成複雜的數學運算</span><span style="font-family: 標楷體; ">。</span><span style="font-family: 標楷體; line-height: 22pt; ">因此本課程目標在於訓練學生如何撰寫MATLAB程式</span><span style="font-family: 標楷體; ">，</span><span style="font-family: 標楷體; line-height: 22pt; ">將自己所知的數學計算方法，整理並規則化，進而利用MATLAB計算其結果。</span></p> <!--EndFragment--></div><div style="font-size: 12px; font-family: tahoma, arial, helvetica, sans-serif; ">教學目標：<br/>1.學習由上而下的設計方法：由上而下的設計方法是鼓勵學生在開始編寫程式碼之前，先思考程式的適當設計，並強調在進行任何工作之前，必須先清楚定義出使用者想要解決的問題，及解決此問題所需要的輸入輸出。<br/>2.學習函式的使用：將完整的工作能合理地分解成更小的子工作。教導函式在隱匿資料上的優點。<br/>3.教導學生如何正確使用MATLAB工具，使得程式寫作及除錯變得更為輕鬆。<br/>4.強調一般常見的程式設計錯誤，以提醒學生避免犯錯。<br/>5.學習MATLAB的資料架構，包括稀疏陣列、單元陣列、以及結構陣列。<br/><br/>教材內容：<br/><a href="http://math.ntnu.edu.tw/~min/matlab/ppt/Chap1_Introduction.ppt" style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); font-size: medium; "><b><font face="標楷體" size="5">MATLAB 簡介</font></b></a><br/><a href="http://math.ntnu.edu.tw/~min/matlab/ppt/Chap2_MATLAB_basics.ppt" style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); font-size: medium; "><b><font face="標楷體" size="5">MATLAB 基本功能介紹</font></b></a><br/><b style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); font-size: medium; "><font face="標楷體" size="5"><a href="http://math.ntnu.edu.tw/~min/matlab/ppt/Chap3_branching_statement.ppt" style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); font-size: medium; ">分支宣告與程式設計</a></font></b></div><div style="font-size: 12px; font-family: tahoma, arial, helvetica, sans-serif; "><b style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); "><font face="標楷體" size="5"><a href="http://math.ntnu.edu.tw/~min/matlab/ppt/Chap4_loops.ppt" style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); ">迴圈</a></font></b></div><div style="font-size: 12px; font-family: tahoma, arial, helvetica, sans-serif; "><b style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); "><font face="標楷體" size="5"><a href="http://math.ntnu.edu.tw/~min/matlab/ppt/Chap5_functions.ppt" style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); ">使用者定義函式</a></font></b></div><div style="font-size: 12px; font-family: tahoma, arial, helvetica, sans-serif; "><a href="http://math.ntnu.edu.tw/~min/matlab/ppt/Chap6_data_plot_types.ppt" style="color: rgb(153, 0, 153); font-family: Arial; background-color: rgb(255, 255, 255); "><b><font face="標楷體" size="5">其它的資料型態與繪圖型態</font></b></a>  </div>'
  },
  '2740': {
    id: '2740',
    department: '科教碩',
    tags: [ '統計', ' 量化', ' ANOVA' ],
    name: '資料分析（一）',
    credit: 3,
    teacher: '譚克平',
    time: "{'1': ['A', 'C'], '2': [], '3': [], '4': [], '5': [], '6': []}",
    location: '臺大總區',
    semester: '108-1',
    description: '<p>1.      Basic ideas of statistics, review of hypothesis testing, tests of normality and detection of outliers, tests of single proportion and the equality of two proportions<!--?xml:namespace prefix = o ns = "urn:schemas-microsoft-com:office:office" /--></p><p>2.      Nonparametric or distribution free tests: differences between parametric and nonparametric tests; Mann-Whitney U test, Wilcoxon’s matched pairs signed-rank test</p><p>3.      Power: the concept of type I error, type II error and power; how to compute power for z tests; factors that affect power; power curves for 1-tailed z test and 2-tailed z test; computation of sample sizes; power tables</p><p>4.      Escalation of type I errors when conducting multiple tests</p><p>5.      Introduction to one way ANOVA: why test variances when the null concerns only means; basic concepts about sum of squares and degrees of freedom; SS between, SS within, SS total; mean squares, F test, follow-up test</p><p>6.      Fisher’s conceptualization of ANOVA, further concepts about sum of squares</p><p>7.      Linear model approach to ANOVA</p><p>8.      Planned comparisons: linear contrasts; degree of freedom of contrast; test of contrasts; trend analysis</p><p>9.      Follow-up tests, multiple comparison, multiplicity: Tukey test, Tukey-Kramer test; Scheffe’s test; Dunnett’s test, Bonferroni’s procedure, Sidak’s procedure, Holm’s procedure</p><p>10.  Two way ANOVA: factorial design; main effects; interaction effect; meaning of interaction; test of interaction and interpretation; simple main effect; fixed model; random model; mixed model</p><p>11.  Repeated measures: one way repeated measures; the meaning and test of sphericity; adjustment of the degrees of freedom by means of the Huynh-Feldt approach and the Greenhouse-Geisser approach; introduction to the multivariate approach, ANOVA approach to reliability of test instrument</p><p>12.  Analysis of covariance: basic assumptions, rationale, and interpretation of printout, Johnson-Neyman approach</p>'
  },
