    description: '<p>我們將介紹資訊科學領域的研究專題，並指導修課學生就其有興趣的題目研究及開發。修課的學生首先要決定方向，學習相關資訊理論並且進行實作。此外，在學期中需繳交進度報告以及在學期末需有成果展示。</p>'
