  '3057': {
    id: '3057',
    department: '資工系',
    tags: [ '數學', ' 程式設計' ],
    name: '數值方法',
    credit: 3,
    teacher: '葉梅珍',
    time: "{'1': [], '2': [], '3': ['7', '8', '9'], '4': [], '5': [], '6': []}",
    location: '臺大總區',
    semester: '108-1',
    description: '<p>本課程介紹基礎的數值方法並強調演算法設計、分析及其應用。我們將探討一些基本和常見的數值分析技術，諸如誤差分析、非線性方程的根求解、線性代數方程求解、內插法、回歸分析。本課程有大量的程式練習。</p>'
  },
  '3058': {
    id: '3058',
    department: '資工系',
    tags: [ 'WebGL', ' 程式設計', ' 圖學' ],
    name: '計算機圖學',
    credit: 3,
    teacher: '張鈞法',
    time: "{'1': [], '2': [], '3': [], '4': ['7', '8', '9'], '5': [], '6': []}",
    location: '師大分部',
    semester: '109-1',
    description: '<p> *** Note that this course requires intensive programming in Javascript, C or C++ (possibly 3,000 to 6,000 lines of code).<br><br>This course is about the programming of 3D computer graphics. During the first half of this course, we will focus on the high-level programming of 3D graphics applications using the OpenGL API. Then, during the second half of this course, we will study the whole process of a 3D renderer, which we will implement as a three-part assignment. There is also a final project. If time allows, we will also cover advanced topics such as texture mapping, GPU programming, global illumination ...etc.<br><br>Part I: Leaning to Drive -- Writing 3D Applications<br>• Overview (1 week)<br>• WebGL &amp; OpenGL Programming (2 weeks)<br>• Transformations (1 week)<br/>• Viewing (1 week)<br/>• Shading (1 week)<br/>Part II: Under the Hood: Implementation of a Renderer<br/>• Geometric Processing (1 week)<br/>• Hidden Surface Removal (1 week)<br/>• Scan Conversion (2 weeks)<br/>• Texture Mapping (1 week)<br/>Part III: Advanced Topics<br/>• GPU Programming, Curves and Surfaces, Animations, Global Illuminations,…etc. (3-4 weeks)</br></br></br></br></br></br></br></p>'
  },
